import { Slider, Rail, Handles, Tracks } from 'react-compound-slider'

const sliderStyle = {  // Give the slider some width
    position: 'relative',
    width: '100%',
    height: 60,
  }
  
  const railStyle = {
    position: 'absolute',
    width: '100%',
    height: 3,
    marginTop: 35,
    borderRadius: 5,
    backgroundColor: '#E8E9EB',
  }

  export function Handle({
    handle: { id, value, percent },
    getHandleProps
  }) {
    return (
      <div
        style={{
          left: `${percent}%`,
          position: 'absolute',
          marginLeft: -9,
          marginTop: 27,
          zIndex: 2,
          width: 18,
          height: 18,
          border: 0,
          textAlign: 'center',
          cursor: 'pointer',
          borderRadius: '50%',
          backgroundColor: '#fff',
          color: '#333',
          boxShadow: '0px 2px 5px #bbb'
        }}
        {...getHandleProps(id)}
      >
        <div style={{ fontFamily: 'Inter', fontSize: 14, marginTop: 25 }}>
          {value}
        </div>
      </div>
    )
  }

  function Track({ source, target, getTrackProps }) {
    return (
      <div
        style={{
          position: 'absolute',
          height: 3,
          zIndex: 1,
          marginTop: 35,
          backgroundColor: '#2f6aff',
          borderRadius: 5,
          cursor: 'pointer',
          left: `${source.percent}%`,
          width: `${target.percent - source.percent}%`,
        }}
        {...getTrackProps() /* this will set up events if you want it to be clickeable (optional) */}
      />
    )
  }
  


const RangeSlider = () => {
    return (
        <div>
            <Slider
                rootStyle={sliderStyle}
                domain={[0, 100]}
                step={1}
                mode={2}
                values={[10, 80]}
            >
                <Rail>
                {({ getRailProps }) => (
                    <div style={railStyle} {...getRailProps()} />
                )}
                </Rail>
                <Handles>
                {({ handles, getHandleProps }) => (
                    <div className="slider-handles">
                    {handles.map(handle => (
                        <Handle
                        key={handle.id}
                        handle={handle}
                        getHandleProps={getHandleProps}
                        />
                    ))}
                    </div>
                )}
                </Handles>
                <Tracks left={false} right={false}>
                {({ tracks, getTrackProps }) => (
                    <div className="slider-tracks">
                    {tracks.map(({ id, source, target }) => (
                        <Track
                        key={id}
                        source={source}
                        target={target}
                        getTrackProps={getTrackProps}
                        />
                    ))}
                    </div>
                )}
                </Tracks>
            </Slider>
          </div>

    )
} 

export default RangeSlider;