import React from 'react'
import './Header.css'
import { NavLink, BrowserRouter } from 'react-router-dom'

const Header = () => {
    return (
        // <div className="header row">
        //     <div className="shares">
        //         <a href="#buy">Buy Shares</a>
        //         <a href="#sell">Sell Shares</a>
        //     </div>
        //     <div className="profile right">
        //         <img src="/Oval.png" alt="" />
        //         <i class="material-icons">keyboard_arrow_down</i>
        //     </div>
        // </div>
        
        <nav class="nav-extended">
            <BrowserRouter>
                <div class="nav-wrapper nav-header">
                    <NavLink className="header-link" to="/buy">Buy Shares</NavLink>
                    <NavLink className="header-link" to="/sell">Sell Shares</NavLink>
                    <div className="profile right">
                        <img src="/Oval.png" class="profile-pic" alt="" />
                        <i class="material-icons">keyboard_arrow_down</i>
                    </div>
                </div>
                <div class="nav-content">
                    <ul class="tabs tabs-transparent">
                    <li class="tab"><NavLink to="/hottest">Hottest Deals</NavLink></li>
                    <li class="tab"><NavLink to="/new">New Deals</NavLink></li>
                    <li class="tab"><NavLink to="/closing">Closing Soon</NavLink></li>
                    </ul>
                </div>
            </BrowserRouter>
        </nav>
    )
}

export default Header;